<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <el-alert
        :closable="false"
        show-icon
        type="warning"
        title=""
      >
      </el-alert>
      <!--  -->
      <el-row class="cat-opt">
        <span></span>
        <!--  -->
        <el-cascader
          v-model="seletedKeys"
          :options="cateList"
          :props="props"
          @change="handleChange"
        ></el-cascader>
      </el-row>

      <!--  -->
      <el-row>
        <el-tabs v-model="activeName" @tab-click="handleTabClick">
          <!--  -->
          <el-tab-pane label="" name="many">
            <!--  -->
            <el-button
              @click="addParamDialogVisible = true"
              type="primary"
              size="mini"
              :disabled="isBtnDiasabled"
            >
              
            </el-button>

            <!--  -->
            <el-table :data="manyTableData" border stripe>
              <!--  -->
              <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-tag
                    v-for="(item, index) in scope.row.attr_vals"
                    :key="index"
                    closable
                    @close="handleClose(index, scope.row)"
                    >{{ item }}</el-tag
                  >
                  <!--  -->
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                    >+ New Tag</el-button
                  >
                </template>
              </el-table-column>
              <!--  -->
              <el-table-column label="#" type="index"></el-table-column>
              <el-table-column
                label=""
                prop="attr_name"
              ></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button
                    type="primary"
                    icon="el-icon-edit"
                    size="mini"
                    @click="showEditDialog(scope.row.attr_id)"
                    ></el-button
                  >
                  <el-button
                    type="danger"
                    @click="removeParams(scope.row.attr_id)"
                    icon="el-icon-edit"
                    size="mini"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
            <!--  -->
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="only">
            <!--  -->
            <el-button
              @click="addParamDialogVisible = true"
              type="primary"
              size="mini"
              :disabled="isBtnDiasabled"
            >
              
            </el-button>
            <!--  -->
            <el-table :data="onlyTableData" border stripe>
              <!--  -->
              <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-tag
                    v-for="(item, index) in scope.row.attr_vals"
                    :key="index"
                    closable
                    @close="handleClose(index, scope.row)"
                    >{{ item }}</el-tag
                  >
                  <!--  -->
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                    >+ New Tag</el-button
                  >
                </template>
              </el-table-column>
              <!--  -->
              <el-table-column label="#" type="index"></el-table-column>
              <el-table-column
                label=""
                prop="attr_name"
              ></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button
                    type="primary"
                    icon="el-icon-edit"
                    size="mini"
                    @click="showEditDialog(scope.row.attr_id)"
                    ></el-button
                  >
                  <el-button
                    type="danger"
                    icon="el-icon-edit"
                    size="mini"
                    @click="removeParams(scope.row.attr_id)"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
            <!--  -->
          </el-tab-pane>
        </el-tabs>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog
      @close="addParamDialogClose"
      :title="'' + dialogTitleText"
      :visible.sync="addParamDialogVisible"
    >
      <div>
        <el-form
          :model="addParamForm"
          ref="addParamFormRef"
          label-width="150px"
          :rules="addParamFormRules"
        >
          <el-form-item :label="dialogTitleText + ':'" prop="attr_name">
            <el-input v-model="addParamForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addParamDialogVisible = false"></el-button>
        <el-button type="primary" @click="addParam"></el-button>
      </span>
    </el-dialog>
    <!--  -->

    <!--  -->
    <el-dialog
      @close="editParamDialogClose"
      :title="'' + dialogTitleText"
      :visible.sync="editParamDialogVisible"
    >
      <div>
        <el-form
          :model="editParamForm"
          ref="editParamFormRef"
          label-width="150px"
          :rules="addParamFormRules"
        >
          <el-form-item :label="dialogTitleText + ':'" prop="attr_name">
            <el-input v-model="editParamForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editParamDialogVisible = false"></el-button>
        <el-button type="primary" @click="editParam"></el-button>
      </span>
    </el-dialog>
    <!--  -->
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      addParamForm: {
        attr_name: ''
      },
      // 
      editParamForm: {
        attr_name: ''
      },
      addParamFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }, // 
      addParamDialogVisible: false, // 
      editParamDialogVisible: false, // 
      activeName: 'many', // 
      cateList: [], // ,
      seletedKeys: [], // 
      onlyTableData: [], // 
      manyTableData: [], // 
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      }
    }
  },
  methods: {
    // 
    async handleChange () {
      if (this.seletedKeys.length !== 3) {
        this.seleted = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      console.log(this.seletedKeys)
      this.getParams()
    },
    // 
    async getParams () {
      //  
      const { data: result } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: {
            sel: this.activeName
          }
        }
      )
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      result.data.forEach(item => {
        item.inputVisible = false // 
        item.inputValue = '' // 
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
      })
      if (this.activeName === 'only') {
        this.onlyTableData = result.data
      } else {
        this.manyTableData = result.data
      }
    },
    // 
    async getCateList () {
      const { data: result } = await this.$http.get('categories')
      if (result.meta.status !== 200) {
        return this.$message.error('')
      }
      this.cateList = result.data
      console.log(this.cateList)
    },
    // 
    handleTabClick () {
      console.log(this.activeName)
      if (!this.isBtnDiasabled) {
        this.getParams()
      }
    },
    // 
    async addParam () {
      this.$refs.addParamFormRef.validate(async valide => {
        if (!valide) return
        const { data: result } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addParamForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (result.meta.status !== 201) {
          return this.$message.error('!!!')
        }

        this.$message.success('')
        this.getParams()
        this.addParamDialogVisible = false
      })
    },
    // 
    addParamDialogClose () {
      this.$refs.addParamFormRef.resetFields()
    },
    // 
    editParamDialogClose () {
      // 
    },
    // 
    async showEditDialog (id) {
      const { data: result } = await this.$http.get(
        `categories/${this.cateId}/attributes/${id}`,
        {
          attr_sel: this.activeName
        }
      )

      if (result.meta.status !== 200) {
        return this.$message.error('!!!')
      }
      this.editParamForm = result.data
      this.editParamDialogVisible = true
    },
    // 
    editParam () {
      this.$refs.editParamFormRef.validate(async valide => {
        if (!valide) return
        const { data: result } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editParamForm.attr_id}`,
          {
            attr_name: this.editParamForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (result.meta.status !== 200) {
          return this.$message.error('!!!')
        }
        this.getParams()
        this.editParamDialogVisible = false
        return this.$message.success('')
      })
    },
    // id 
    removeParams (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: result } = await this.$http.delete(
            `categories/${this.cateId}/attributes/${id}`
          )
          if (result.meta.status !== 200) {
            return this.$message.error('!!!')
          }
          this.getParams() // 
          this.$message({
            type: 'success',
            message: '!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // enter 
    async handleInputConfirm (item) {
      if (item.inputValue.trim().length === 0) return (item.inputValue = '')

      // 
      item.attr_vals.push(item.inputValue.trim())
      item.inputValue = ''
      item.inputVisible = false
      this.saveAttrVals(item)
    },
    // 
    showInput (item) {
      item.inputVisible = true
      // 
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 
    async saveAttrVals (item) {
      const { data: result } = await this.$http.put(
        `categories/${this.cateId}/attributes/${item.attr_id}`,
        {
          attr_name: item.attr_name,
          attr_sel: item.attr_sel,
          attr_vals: item.attr_vals.join(' ')
        }
      )
      if (result.meta.status !== 200) {
        return this.$message.error('!!!')
      }
      this.$message.success('')
    },
    handleClose (index, item) {
      item.attr_vals.splice(index, 1)
      this.saveAttrVals(item)
    }
  },
  created () {
    // 
    this.getCateList()
  },
  computed: {
    //  false true
    isBtnDiasabled () {
      return this.seletedKeys.length !== 3
    },
    // id
    cateId () {
      return this.seletedKeys.length !== 3 ? null : this.seletedKeys[2]
    },
    dialogTitleText () {
      return this.activeName === 'only' ? '' : ''
    }
  }
}
</script>

<style lang="less" scoped>
.cat-opt {
  margin: 20px 0;
}
.el-tag {
  margin: 10px;
}
.input-new-tag {
  width: 120px;
  padding: 0 10px;
}
</style>
